import React, { useState, useEffect, useRef } from "react";
import "./Inventory.css";
import { getAccount, getImage } from "./Mint/MintUtility";
import Web3 from "web3";
const ABIS = require("./ABI/Abis.json");

export default function Inventory({ height }) {
  const [web3State, setWeb3State] = useState(undefined);
  const [account, setAccount] = useState(undefined);
  const [TMAContract, setTMAContract] = useState(undefined);
  const [NFTImages, setNFTImages] = useState([]);

  useEffect(() => {
    async function initWeb3() {
      let _web3 = new Web3(window.ethereum);

      let GSNFTContract = new _web3.eth.Contract(
        ABIS["TheModernAcreNFTPass"].ABI,
        ABIS["TheModernAcreNFTPass"].Address
      );

      let account = await getAccount(_web3);

      setWeb3State(_web3);
      setAccount(account);
      setTMAContract(GSNFTContract);
      getImage(_web3, setNFTImages, GSNFTContract);
    }

    initWeb3();
  }, []);

  useEffect(() => {
    console.log(NFTImages);
  }, [NFTImages]);

  return (
    <div style={height ? { height: `${height}vh` } : {}} className="inventoryGrid">
      {NFTImages.map((image) => (
        <img src={image} alt="nftImage"></img>
      ))}
    </div>
  );
}
