import logo from "./logo.svg";
import "./App.css";
import { Routes, Route, useNavigate } from "react-router-dom";
import Mint from "./Mint/Mint.js";
import Owner from "./Owner/Owner";
import Verify from "./Verify/Verify";
import { useEffect } from "react";
import { toast } from "react-toastify";

function App() {
  return (
    <div className="App">
      <nav>
        <img
          src="https://themodernacre.com/wp-content/uploads/2020/01/The-Modern-Acre_Wordmark.png"
          alt="The Modern Acre"
        ></img>
        <button
          className="appButton"
          style={{ float: "right", width: "10vw", marginTop: "40px", marginRight: "100px" }}
          onClick={async () => {
            if (window.ethereum) {
              if (window.ethereum.chainId === "0x4") {
                toast.success("Already Connected");
              }
              try {
                // check if the chain to connect to is installed
                await window.ethereum.request({
                  method: "wallet_switchEthereumChain",
                  params: [{ chainId: "0x4" }], // chainId must be in hexadecimal numbers
                });

                await window.ethereum.request({ method: "eth_requestAccounts" });
              } catch (error) {
                // This error code indicates that the chain has not been added to MetaMask
                // if it is not, then install it into the user MetaMask
                if (error.code === 4902) {
                  try {
                    await window.ethereum.request({
                      method: "wallet_addEthereumChain",
                      params: [
                        {
                          chainId: "0x4",
                          rpcUrl: "https://rinkeby.infura.io/v3/",
                        },
                      ],
                    });
                  } catch (addError) {
                    console.error(addError);
                  }
                }
                console.error(error);
              }
            } else {
              // if no window.ethereum then MetaMask is not installed
              alert("MetaMask is not installed. Please consider installing it: https://metamask.io/download.html");
            }
          }}
        >
          Connect Wallet
        </button>
      </nav>
      <Routes>
        <Route path="/" element={<Mint />}></Route>
        <Route path="/owner" element={<Owner />}></Route>
        <Route path="/adminDashboard" element={<Verify />}></Route>
        <Route path="/0.json" render={"0.json"}></Route>
        <Route path="/1.json" render={"1.json"}></Route>
        <Route path="/2.json" render={"2.json"}></Route>
        <Route path="/3.json" render={"3.json"}></Route>
        <Route path="/4.json" render={"4.json"}></Route>
        <Route path="/5.json" render={"5.json"}></Route>
        <Route path="/6.json" render={"6.json"}></Route>
        <Route path="/Barnhouse.jpg" render={"Barnhouse.jpg"}></Route>
        <Route path="/DollarBill.jpg" render={"DollarBill.jpg"}></Route>
        <Route path="/Farmland.jpg" render={"Farmland.jpg"}></Route>
        <Route path="/MentorPass.jpg" render={"MentorPass.jpg"}></Route>
        <Route path="/RoboticArm.jpg" render={"RoboticArm.jpg"}></Route>
        <Route path="/ToTheMoonWine.jpg" render={"ToTheMoonWine.jpg"}></Route>
        <Route path="/Tractor.jpg" render={"Tractor.jpg"}></Route>
      </Routes>
    </div>
  );
}

export default App;
