import React, { useState, useEffect, useRef } from "react";
import {
  getAccount,
  getImage,
  airdropMentor,
  airdropMentee,
  reserve,
  transfer,
  withdrawFunds,
  setPresale,
} from "./OwnerUtility.js";
import "./Owner.css";
import "../App.css";
import Web3 from "web3";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Inventory from "../Inventory.js";

const ABIS = require("../ABI/Abis.json");

function Images({ image, setImage }) {
  return (
    <div className="ownerMenteeGrid">
      <img
        className="tabImage"
        style={image == 0 ? { border: "yellow 1px solid" } : {}}
        onClick={() => {
          if (image == 0) setImage(null);
          else setImage(0);
        }}
        src="Barnhouse.jpg"
        alt="Barnhouse"
      />
      <img
        className="tabImage"
        style={image == 1 ? { border: "yellow 1px solid" } : {}}
        onClick={() => {
          if (image == 1) setImage(null);
          else setImage(1);
        }}
        src="DollarBill.jpg"
        alt="DollarBill"
      />
      <img
        className="tabImage"
        style={image == 2 ? { border: "yellow 1px solid" } : {}}
        onClick={() => {
          if (image == 2) setImage(null);
          else setImage(2);
        }}
        src="Farmland.jpg"
        alt="Farmland"
      />
      <img
        className="tabImage"
        style={image == 3 ? { border: "yellow 1px solid" } : {}}
        onClick={() => {
          if (image == 3) setImage(null);
          else setImage(3);
        }}
        src="RoboticArm.jpg"
        alt="RoboticArm"
      />
      <img
        className="tabImage"
        style={image == 4 ? { border: "yellow 1px solid" } : {}}
        onClick={() => {
          if (image == 4) setImage(null);
          else setImage(4);
        }}
        src="ToTheMoonWine.jpg"
        alt="ToTheMoonWine"
      />
      <img
        className="tabImage"
        style={image == 5 ? { border: "yellow 1px solid" } : {}}
        onClick={() => {
          if (image == 5) setImage(null);
          else setImage(5);
        }}
        src="Tractor.jpg"
        alt="Tractor"
      />
    </div>
  );
}

function MintComponent({ message, web3State, TMAContract, onClick, image, reserveQuantRef }) {
  const addressRef = useRef(
    message != "Reserve" ? null : { current: { value: "0x0724668E123A4888dFa854ce452b40d28540001D" } }
  );

  return (
    <div>
      {message != "Reserve" ? (
        <input className="addressInput" ref={addressRef} type="text" placeholder="Address: 0x..." />
      ) : (
        <></>
      )}
      <button
        className="mintButton"
        onClick={() => {
          if (image != null) {
            onClick(addressRef.current.value, image, TMAContract, web3State);
          } else if (reserveQuantRef != null) {
            onClick(addressRef.current.value, TMAContract, web3State, reserveQuantRef.currentTarget.value);
          } else {
            onClick(addressRef.current.value, TMAContract, web3State);
          }
        }}
      >
        {message}
      </button>
    </div>
  );
}

function Mentor({ web3State, TMAContract }) {
  return (
    <div className="ownerDisplay" name="ownerDisplay">
      <div className="ownerMentor">
        <MintComponent
          message={"Airdrop"}
          web3State={web3State}
          TMAContract={TMAContract}
          onClick={airdropMentor}
          image={null}
        ></MintComponent>
      </div>
    </div>
  );
}
function Mentee({ web3State, TMAContract }) {
  const [image, setImage] = useState(null);

  return (
    <div className="ownerDisplay" name="ownerDisplay">
      <div className="ownerMentee">
        <Images image={image} setImage={setImage}></Images>
        <MintComponent
          message={"Airdrop"}
          web3State={web3State}
          TMAContract={TMAContract}
          onClick={airdropMentee}
          image={image}
        ></MintComponent>
      </div>
    </div>
  );
}
function Reserve({ web3State, TMAContract }) {
  const [image, setImage] = useState(null);
  const reserveQuantRef = useRef(null);

  return (
    <div className="ownerDisplay" name="ownerDisplay">
      <div className="ownerReserve">
        <Images image={image} setImage={setImage}></Images>
        <input
          ref={reserveQuantRef}
          type={"number"}
          onChange={(e) => {
            if (e.currentTarget.value < 0) {
              e.currentTarget.value = 0;
            } else if (e.currentTarget.value > 10) {
              e.currentTarget.value = 10;
            }
          }}
          placeholder="Quantity to reserve..."
        ></input>
        <MintComponent
          message={"Reserve"}
          web3State={web3State}
          TMAContract={TMAContract}
          onClick={reserve}
          image={image}
          reserveQuantRef={reserveQuantRef}
        ></MintComponent>
      </div>
    </div>
  );
}
function Transfer({ web3State, TMAContract }) {
  return (
    <div className="ownerDisplay" name="ownerDisplay">
      <Inventory height={70}></Inventory>
      <div style={{ display: "inline-block", marginLeft: "5vw" }} className="ownerTransfer">
        <MintComponent message={"Transfer"} web3State={web3State} TMAContract={TMAContract}></MintComponent>
        <button
          className="mintButton"
          onClick={() => {
            withdrawFunds(TMAContract, web3State);
          }}
        >
          Withdraw Funds
        </button>
        <button
          className="mintButton"
          onClick={() => {
            setPresale(TMAContract, web3State);
          }}
        >
          End Pre-sale Mint
        </button>
      </div>
    </div>
  );
}

let tabActiveStyle = {
  fontSize: "larger",
  color: "#5bc09a",
  backgroundColor: "#ffffff",
};

export default function Owner() {
  const [web3State, setWeb3State] = useState(undefined);
  const [account, setAccount] = useState(undefined);
  const [TMAContract, setTMAContract] = useState(undefined);
  const [NFTImages, setNFTImages] = useState([]);
  const [tabState, setTabState] = useState("Mentor");

  useEffect(() => {
    async function initWeb3() {
      let _web3 = new Web3(window.ethereum);

      let GSNFTContract = new _web3.eth.Contract(
        ABIS["TheModernAcreNFTPass"].ABI,
        ABIS["TheModernAcreNFTPass"].Address
      );

      let _account = await getAccount(_web3);

      setWeb3State(_web3);
      setAccount(_account);
      setTMAContract(GSNFTContract);
    }

    initWeb3();
  }, []);

  useEffect(() => {
    console.log(NFTImages);
  }, [NFTImages]);

  return (
    <>
      <div className="ownerWrapper">
        <div className="ownerGrid">
          <div
            className="ownerTab"
            name="tab1"
            style={tabState == "Mentor" ? tabActiveStyle : {}}
            onClick={() => {
              setTabState("Mentor");
            }}
          >
            Mentor
          </div>
          <div
            className="ownerTab"
            name="tab2"
            style={tabState == "Mentee" ? tabActiveStyle : {}}
            onClick={() => {
              setTabState("Mentee");
            }}
          >
            Member
          </div>
          <div
            className="ownerTab"
            name="tab3"
            style={tabState == "Reserve" ? tabActiveStyle : {}}
            onClick={() => {
              setTabState("Reserve");
            }}
          >
            Reserve
          </div>
          <div
            className="ownerTab"
            name="tab4"
            style={tabState == "Transfer" ? tabActiveStyle : {}}
            onClick={() => {
              setTabState("Transfer");
            }}
          >
            Transfer/Withdraw
          </div>
          {tabState == "Mentee" ? (
            <Mentee web3State={web3State} TMAContract={TMAContract} />
          ) : tabState == "Reserve" ? (
            <Reserve web3State={web3State} TMAContract={TMAContract} />
          ) : tabState == "Transfer" ? (
            <Transfer web3State={web3State} TMAContract={TMAContract} />
          ) : (
            <Mentor web3State={web3State} TMAContract={TMAContract} />
          )}
        </div>
      </div>
      <ToastContainer />
    </>
  );
}
