import Web3 from "web3";
import { toast } from "react-toastify";

export async function getAccount(_web3) {
  return (await _web3.eth.getAccounts())[0];
}

export async function getImage(web3, setNFTImages, TheModernAcreNFTPass) {
  const account = await getAccount(web3);

  let balanceOf = await TheModernAcreNFTPass.methods.balanceOf(account).call({ from: account });
  let _images = [];
  for (let i = 0; i < balanceOf; i++) {
    let tokenId = await TheModernAcreNFTPass.methods.tokenOfOwnerByIndex(account, i).call({ from: account });
    let data = await fetch(await TheModernAcreNFTPass.methods.tokenURI(tokenId).call({ from: account }), {
      method: "GET",
      headers: { "Content-Type": "application/json" },
    });
    let tokenJSON = await data.json();
    let tokenURI = tokenJSON.image;
    console.log(tokenURI);
    let imageBlob = await (await fetch(tokenURI)).blob();
    const imageURL = URL.createObjectURL(imageBlob);
    _images.push(imageURL);
  }

  setNFTImages(_images);
}
export async function airdropMentor(address, TheModernAcreNFTPass, web3) {
  const account = await getAccount(web3);

  await TheModernAcreNFTPass.methods.airdropMentorPass(address).send({ from: account });
}

