import React, { useState, useEffect } from "react";
import SmartContracts from "../SmartContractInterface/SmartContracts";
import "./Verify.css";
import Web3 from "web3";
import "../App.css";

const ABIS = require("../ABI/Abis.json");
const VERIFIED_ACCOUNTS = require("../VerifiedAddresses.json");

export default function Verify() {
  const [web3State, setWeb3State] = useState(undefined);
  const [account, setAccount] = useState(undefined);
  const [verified, setVerified] = useState(false);

  async function getAccount(_web3 = undefined) {
    if (_web3) return (await _web3.eth.getAccounts())[0];
    return (await web3State.eth.getAccounts())[0];
  }

  useEffect(() => {
    async function initWeb3() {
      let _web3 = new Web3(window.ethereum);

      for (let contractName of Object.keys(ABIS)) {
        if (contractName !== "HttpProvider") {
          let contractData = ABIS[contractName];
          if (contractData.Address === "" || contractData.ABI.length === 0) {
            if (contractName == "SmartContract Name") {
              alert("You have not added your Smart Contract into the ABI Yet");
            } else {
              alert(`Contract ${contractName} has invalid ABI or Address`);
              return;
            }
          }
          let SmartContract = new _web3.eth.Contract(contractData.ABI, contractData.Address);
          ABIS[contractName]["SmartContract"] = SmartContract;
        }
      }

      setWeb3State(_web3);
      setAccount(getAccount(_web3));
    }
    initWeb3();
  }, []);

  async function attemptVerify() {
    let _account = await getAccount();
    setAccount(_account);
    for (let verifiedAccount of VERIFIED_ACCOUNTS.accounts) {
      if (verifiedAccount === _account) {
        setVerified(true);
        return;
      }
    }
    alert(`${_account} is not authorized to use the Admin Dashboard`);
  }

  return (
    <>
      {verified ? (
        <SmartContracts ABIS={ABIS} account={account} />
      ) : (
        <div className="verifyWrapper">
          <button className="appButton verifyButton" onClick={() => attemptVerify()}>
            Verify
          </button>
        </div>
      )}
    </>
  );
}
